import React from 'react';
import TodoHeader from './TodoHeader';
import TodoFooter from './TodoFooter';
import TodoList from './TodoList';
const ENTER = 13;

class App extends React.Component {
  constructor() {
    super();

    this.state = {
      todos: [],
      inputValue: '',
      fiteredBy: '',
      allCompleted: false
    };
  };

  componentDidMount() {
    this.setState({ filteredBy: 'all' })
    //     ,
    //     filteredBy  'undefined'
  }

  addTodo = (event) => {
    event.persist();
    const { value } = event.target;

    if (event.keyCode === ENTER && value.length !== 0) { //   enter
      if (!this.state.todos.find(todo => todo.title === value))
      { //   todo 
        this.setState((prevState) => ({
          todos: [ //   
            ...prevState.todos,
            { title: value, completed: false }
          ], 
          inputValue: '', //  input
          allCompleted: false //    
        }));
      } else {
        this.setState({
          inputValue: '' //   todo :  input,   
        })
      }
    } else { //    
      this.setState({ inputValue: value });
    }; 
  };

  deleteTodo = (title) => {
    this.setState((prevState) => ({
      todos: prevState.todos.filter(todo => todo.title !== title)
    }));

    if (this.state.todos.length === 1) { //    
      this.setState({ allCompleted: false }) //  'select all' input
    }
  };

  toggleTodoComplete = (title) => {
    const { todos, allCompleted } = this.state;
    console.log(todos.find(todo => todo.title === title).completed)

    this.setState((prevState) => ({
      todos: prevState.todos.map(todo => (
        todo.title === title
          ? { title: todo.title, completed: !todo.completed }
          : todo
      ))
    }));

    if (todos.filter(todo => !todo.completed).length === 1)
    { //    
      this.setState({ allCompleted: true }); //  'select all' input
    };

    if (todos.find(todo => todo.title === title).completed || allCompleted)
    { //       
      this.setState({ allCompleted: false });
    }; //      -  'select all' input
  };

  setAllAsCompleted = () => {
    if (this.state.todos.length !== 0) { //   ,  - 
      this.setState((prevState) => ({
        todos: prevState.todos.map(todo => ({ ...todo, completed: true })),
        allCompleted: true
      }));
    };
  };

  setFilterAttribute = (attribute) => {
    this.setState({ filteredBy: attribute });
  };

  filterTodoList = (todos) => {
    return todos.filter(todo => {
      switch (this.state.filteredBy) {
        case 'active':
          return !todo.completed;

        case 'completed':
          return todo.completed;

        default:
          return true;
    }})
  };

  deleteAllCompleted = () => {
    this.setState((prevState) => ({
      todos: prevState.todos.filter(todo => !todo.completed)
    }));

    if (!this.state.todos.find(todo => !todo.completed)) 
    { //    
      this.setState({ allCompleted: false }) //  'select all' input
    };
  };

  render() {
    const { todos, inputValue, allCompleted, filteredBy } = this.state;

    return (
      <section className="todoapp">
        <TodoHeader
          addTodo={this.addTodo}
          inputValue={inputValue}
        />

        <TodoList
          todos={todos}
          setAllAsCompleted={this.setAllAsCompleted}
          allCompleted={allCompleted}
          filterTodoList={this.filterTodoList}
          deleteTodo={this.deleteTodo}
          toggleTodoComplete={this.toggleTodoComplete}
        />

        <TodoFooter
          todos={todos}
          filteredBy={filteredBy}
          setFilterAttribute={this.setFilterAttribute}
          deleteAllCompleted={this.deleteAllCompleted}
        />
      </section>
    );
  };
};

export default App;


